.navbar {
  background: #64a7f3;
  min-height: 30px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  z-index: 99;
  display:inline-block;
  /* margin-bottom:10px; */
  font-size:25px;
}

.navbar ul {
  text-align: center;
  margin: 0 auto;
}

.navbar li, a {
  /* display: inline-block; */
  padding: 0px 0px;
  /* width: 33.3%; */
  margin: 0 50px 0 50px;
  /* text-decoration: none; */
  color: white;
  text-align: center;
  display: inline;
}

body{
  padding-top:80px;
}

@media (max-width: 1295px) {
  .navbar {
    background: #64a7f3;
    position: fixed;
    width: 100%;
  }
  .navbar ul {
    text-align: center;
    margin: 0;
    padding:0;
  }
  
  .navbar li,a {
    /* display: inline-block; */
    padding: 0;
    margin: 0 20px 0 20px;
    /* width: 33.3%; */
    font-size:18px;
    /* text-decoration: none; */
    color: white;
    text-align: center;
  }

  body{
    position:relative;
    padding-top:11%;
  }
}

@media (max-width: 420px) {
  .navbar li:first-child {
    width: 25%;
    font-size:20px;
  }

  .navbar li:nth-child(2) {
    width: 35%;
    font-size:20px;
  }

  .navbar li:last-child {
    width: 40%;
    font-size:20px;
  }
  body{
    padding-top:0px;
  }
}

body{
  padding-top:40px;
}

.header {
  margin-top:10px;
  background: #5d5f5f;
  height: 100px;
  color: white;
  margin-bottom:10px;
}

.header h2 {
  margin-top: 60px;
  padding-top: 20px;
  text-align: center;
  color: white;
  font-size: 30px;
}

/* .header div {
  font-size: 16px;
  text-align: center;
} */

.header li{
  text-align: center;
  display: inline;
  margin-right:28px;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color:  #64a7f3;
  text-align: left;
  font-size: 10px;
}

.footer a {
  color: white;
}


